import React, { useState } from "react";
import {
  HashRouter as Router,
  Link,
  Switch,
  Route,
  useParams,
  useHistory,
} from "react-router-dom";
import "./Form.css";
class Form extends React.Component {
  constructor(props) {
    super(props);
    this.state = { valuename: "" };
  }
  render() {
    return (
      <div>
        <Router>
          <header className="mx-auto  d-flex align-items-center justify-content-center">
            <nav className="w-50 d-flex justify-content-between">
              <Link className="link" to="/Home">
                Home
              </Link>
              <Link className="link" to="/Routes">
                Routes
              </Link>
              <Link className="link" to="/About-Us">
                About-Us
              </Link>
            </nav>
          </header>
          <div className="content">
            <Switch>
              <Route path="/About-Us">
                <AboutUs />
              </Route>
              <Route path="/Routes">
                <ContactUs />
              </Route>
              <Route path="/Home">
                <Home />
              </Route>
              <Route path="*">
                <Error />
              </Route>
            </Switch>
          </div>
        </Router>
      </div>
    );
  }
}

function Error() {
  return (
    <div>
      <h1>404 not found</h1>
    </div>
  );
}

function Home() {
  return (
    <div>
      <h1>Hello from home</h1>
    </div>
  );
}

function ContactUs() {
  const [route, setRoute] = useState("");
  let history = useHistory();
  function valueOnChange(event) {
    const name = event.target.name;
    const value = event.target.value;
    setRoute(value);
  }
  function valueOnSubmit(event) {
    event.preventDefault();
    history.push(`/${route}`);
  }
  return (
    <div>
      <h1>Hello from Routes</h1>
      <div>
        <form onSubmit={(e) => valueOnSubmit(e)}>
          <input name="valuename" onChange={(e) => valueOnChange(e)} />
          <input type="submit" />
        </form>
      </div>
    </div>
  );
}
function AboutUs() {
  return (
    <div>
      <h1>Hello from About Us</h1>
    </div>
  );
}
function Welcome() {
  return (
    <div>
      <h1>Welcome from Footer</h1>
    </div>
  );
}

function CopyRights() {
  return (
    <div>
      <h1>These are the Copy Rights</h1>
    </div>
  );
}

function Patent() {
  return (
    <div>
      <h1>Welcome from Patent</h1>
    </div>
  );
}

Form.propTypes = {};
export default Form;
